.App {
  text-align: center;
  background-color: #121212;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes fadein {
 from {opacity: 0; transform:translateX(20px);}
 to {opacity: 1; transform: translateX(0);}  
}
.fade-in {
  opacity: 0;
  animation: fadein 1s forwards;
}
.delay1{animation-delay: 0s;}
.delay2{animation-delay: 0.5s;}
.delay3{animation-delay: 1s;}
.delay4{animation-delay: 1.5s;}

/* ---------------- NAVBAR ---------------- */
.Navbar {
  width: 100%;
  height: 9vh;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5rem;
  flex-wrap: wrap;
  padding: 0.5rem;
  position: sticky;
  top: 0;
  z-index: 1000;
  background-color: #1f1f1f;
  transition: max-height 0.3s ease-in-out;
}

.Navbar a {
  color: white;
  text-decoration: none;
}

/* HAMBURGER ICON */
.hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
  position: absolute;
  top: 15px;
  right: 20px;
}

.hamburger span {
  height: 3px;
  width: 25px;
  background: white;
  margin: 4px 0;
  border-radius: 5px;
}

/* MOBILE RESPONSIVE NAVBAR */
@media (max-width: 768px) {
  .Navbar {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
    padding: 1rem;
    max-height: 0;
    overflow: hidden;
  }
.Intro-about{
  flex:1 1 100%;
  min-height: 530px;
  margin: 0.8rem 0;
  min-width: 400px;
  margin-right:79px;
  margin-left:32px;
}

  .Navbar.active {
    max-height: 300px; /* expands when active */
  }

  .hamburger {
    display: flex;
  }
  .Home {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  flex: 1 1 100%;
  min-height: 120px;
}
.Home button{
 width: 14vw;
 gap:0.5rem;
 height: 9vh;
}
  .About-heading {
    width: 100vw !important;      /* force full viewport width */
    text-align: center;
    display: block;
    margin: 15px 0;
  }

  .About {
    width: 100vw !important;      /* ensure parent is also full width */
    justify-content: center;
    font-size: 10px;
    padding: 28px;
  }

  body, html, #root, .App {
    width: 100vw;
    overflow-x: hidden;           /* prevent sideways scroll */
  }

.skill
{
  flex: 1 1 100%;
  min-height: 120px;
}
.edu{
