/* ---------------- OTHER STYLES ---------------- */
.Info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  color: #ffffff;
  transition: all 0.3s ease;
}
.Picture
 {
   width: 50%; 
  }
.Home {
  height: 100%;
  display: flex;
  justify-content: space-between;/*This added space between two divs*/
  align-items: center;
  background-color: #121212;
}
.Button {
  display: flex;
  gap:20px;
  margin-top: 12px;
  margin-bottom: 12px;
}
.delay4 {
  cursor: pointer;
  background-color: #77c5f3;
  color:black;
  border-radius: 10px;
  border: none;
  width: 10vw;
  height: 6vh;
  transition: all 0.3s ease;
}
.delay4 a { color: black; }
.delay4:hover,
.delay4 a:hover {
  color:white;
  background-color: #121212;
  transform: scale(1.1);
}
.Picture img {
  height:77vh;
  transition: all 0.3s ease;
  border-radius: 4%;
  transform-origin:center center ;
}
.Picture img:hover {
  transform: scale(3.2) translateY(-5px);
  box-shadow: 0 0 10px 2px white;
}
.Info:hover {
  transform: scale(0.98);
  box-shadow: 0 0 10px 2px #121212;
  border-radius: 12%;
}
.Skills {
  background-color: #121212;
  height: 100%;
  display: flex;
  justify-content: space-around;
  gap:2 rem;
  flex-wrap: wrap;
  color: white;
}
.skill {
  border-top: 2px solid #ff4d4d;
  border-left: 2px dashed white;
  border-radius: 12%;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 100%;
  flex:1 1 200%;
  min-height: 130px;
  box-sizing: border-box;
  margin-top: 72px;
  max-width: 500px;
  padding: 2rem;
}
.skill:hover {
  transform: translateY(-5px) scale(1.02);
  box-shadow: 0 10px 20px silver;
}
.Project {
  border-top: 2px solid #ff4d4d;
  border-left: 2px dashed white;
  background-color:#121212;
  border-radius: 12%;
  flex:1 1 280px;
  max-width: 450px;
  width: 50%;
  text-align: center;
  height: 70%;
  min-height: 400px;
  padding: 1.5rem;
  box-sizing: border-box;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.Project:hover {
  transform: translateY(-5px) scale(1.02);
  box-shadow: 0 10px 20px silver;
}
.Projects {
  background-color: #121212;
  display: flex;
  gap:2 rem;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 2 rem;
  margin-top: 100px;
